<!--
    @页面名称：(中介)公司管理列表页
    @作者：豆亚东 (douyadong@lifang.com)
    @业务逻辑说明：
        1.
        2.        
-->
<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;">
                <el-button type="primary" @click="addCompany">+增加公司</el-button>
            </div>     
            <el-form ref="form" :model="form" label-width="180px" class="gap-2">
                <el-row >
                    <el-col :span="12">
                        <el-form-item label="公司所属城市" prop="cityList">
                            <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                        </el-form-item>
                        <el-form-item label="合作时间" prop="cooperationStart">
                            <el-date-picker
                                v-model="form.cooperationStart"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="业务" prop="businessType">
                            <el-select v-model="form.businessType" filterable>
                                <el-option label="新房和二手房" value="3"></el-option>
                                <el-option label="新房" value="1"></el-option>
                                <el-option label="二手房" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="门店地址" prop="storeAddress">
                            <el-input v-model="form.storeAddress"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="公司名称" prop="name">
                            <el-input v-model="form.name"></el-input>
                        </el-form-item>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="到期查询" prop="searchType">
                                    <el-select v-model="form.searchType" filterable>
                                        <el-option label="请选择查询方式" value="0"></el-option>
                                        <el-option label="按即将到期天数查询" value="1"></el-option>
                                        <el-option label="按合作结束日期查询" value="2"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==1">
                                <el-form-item prop="searchDay" class="expire">
                                    <el-input v-model="form.searchDay" placeholder="请输入整数" style="width:200px;"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==2">
                                <el-form-item prop="searchDate" class="expire">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeStart"
                                        type="date"
                                        placeholder="选择日期"
                                        style="width:200px"
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        
                        <el-form-item label="门店名称" prop="storeName">
                            <el-input v-model="form.storeName"></el-input>
                        </el-form-item>
                        <el-form-item label="代理商" prop="agency">
                            <el-input v-model="form.agency"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="resetForm">重置</el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search">搜索</el-button>
                </el-col>
            </el-row>
            <div class="search-result">共搜索到 {{summary.companyTotal}}家公司，{{summary.hATotal}}家有代理商，{{summary.nATotal}}家无代理商</div>
            <el-table :data="companyInfoList" border style="width: 100%">
                <el-table-column type="index" label="序号" align="center" width="100" ></el-table-column>
                <el-table-column prop="city" label="城市" align="center" ></el-table-column>
                <el-table-column prop="companyName" label="中介公司全称" align="center" ></el-table-column>
                <el-table-column prop="companyAbbreviation" label="中介公司简称" align="center" ></el-table-column>
                <el-table-column prop="agencyName" label="代理商公司" align="center"></el-table-column>
                <el-table-column prop="cooperationStart" label="有效期始" align="center"></el-table-column>
                <el-table-column prop="cooperationEnd" label="有效期止" align="center"></el-table-column>
                <el-table-column prop="bd" label="bd" align="center"></el-table-column>
                <el-table-column prop="createTime" label="创建时间" align="center"></el-table-column>
                <el-table-column prop="creater" label="创建人" align="center"></el-table-column>
                <el-table-column prop="name" label="操作" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="editorCompany(scope.$index, scope.row)" type="text">编辑|</el-button>
                        <el-button size="mini" @click="bankAccount(scope.$index, scope.row)" type="text">分佣账号设置|</el-button>
                        <el-button size="mini"  type="text" @click="handleEnd(scope.$index, scope.row)">终止合作</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="storeAccount" label="门店" align="center"></el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="form.currentPage"
                    :page-sizes="[10, 20, 50, 100,500]"
                    :page-size="form.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
            <el-dialog title="终止公司合作" :visible.sync="firstDialogVisible" width="30%" >
                <p>1、对公司通知合作，旗下的门店也将一起会被停止合作</p>
                <p>2、终止合作门店下的经纪人账号将会被冻结</p>
                <p>3、公司和门店被停止合作后将无法重新再被恢复</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="firstDialogVisible = false,secondDialogVisible = true">以了解风险，下一步</el-button>
                </span>
            </el-dialog>
            <el-dialog title="终止公司合作" :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="请添加终止合作原因" v-model="remark" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="secondDialogVisible = false,continueJoin()">取 消</el-button>
                    <el-button type="primary" @click="secondDialogVisible = false,endJoin()" >确 定</el-button>
                </span>
            </el-dialog>
            <!--编辑公司组件-->
            <editor-company ref="editor" :currentCompanyInfo="currentCompanyInfo" :title="title" @editSuccess='editSuccess' @addSuccess='addSuccess'></editor-company>
            <!--分佣账号组件-->
            <commission ref="commission" :currentCompanyInfo="currentCompanyInfo" ></commission>
        </el-main>
    </el-container> 
</template>

<script>
import EditorCompany from '@/components/company/_EditorCompany';
import Commission from '@/components/company/_Commission';
import BreadCrumb from '@/components/common/BreadCrumb';
import Region from '@/components/common/Region';

export default {
    name: 'CompanyManagement',
    components:{EditorCompany,Commission,BreadCrumb,Region},
    data () {
        return {
            startLevel:1,//二级联动城市传参
            endLevel:2,//二级联动城市传参
            firstDialogVisible: false,//第一个终止合作弹出框
            secondDialogVisible:false,//第二个终止合作弹出框
            remark:'',//终止合作原因
            summary:{},//summary信息
            companyInfoList:[],//公司列表信息
            companyInfoIndex:'',//操作公司时该公司处于所有列表的位置
            currentCompanyInfo:'',//当前编辑的公司信息
            title:'',//判断是编辑公司还是添加公司
            total:400,//根据查询信息查询到的信息总数;
            currentPage:1,//默认当前页为1
            pageSize:10,//默认显示10条信息
            // 表单查询信息
            form:{
                agency:'',//代理商名称
                businessType:'',//公司业务类型,空为未选择，1.新房，2.二手房，3.新房＋二手房
                cityId:'',//所属城市Id
                cityList:[],//城市二级联动所需
                cooperationStart:[],//合作时间段
                name:'',//公司名称
                searchDate:'',//到期日期
                searchDay:'',//即将到期天数 
                searchType:'',//到期查询方式
                storeAddress:'',//门店地址
                storeName:''//门店名称
            },
            breadCrumb: [{text:'加盟管理'},{text: "公司管理"}],//面包屑
        }
    },
    created(){
        this.requestList();
        this.requestSummary();
    },
    methods:{
        // 子组件添加公司成功之后，传递给父组件的值;
        addSuccess(addInfo){
            // 第一种再次发送请求，同时表单查询重置;
            this.$refs.form.resetFields();
            this.requestList();

            //第二种
            // this.tableData.unshift(addInfo);
            // this.currentPage=1;
            // this.pageSize=10;
            // this.total++;
        },
        resetForm(formName) {
            this.$refs.form.resetFields();
        },
        //根据表单信息搜索
        search(){
            this.requestList();
            this.requestSummary();
        },
        //每页多少条
        handleSizeChange(val) {
            this.pageSize=val;
            // 状态改变发送请求
            this.requestList();
        },
        //当前页
        handleCurrentChange(val) {
            this.currentPage=val;
            //状态改变发送请求
            this.requestList();
        },
        // 添加公司
        addCompany(){
            // 调用子组件方法，显示对话框 
            this.title='增加公司';            
            setTimeout(()=>{
                this.$refs.editor.open();
            },200); 
        },
        //编辑
        editorCompany(index, row){
            console.log(row,'编辑信息')
            //操作公司时，该公司所处所有信息列表的位置;
            this.companyInfoIndex=(this.currentPage-1)*this.pageSize+index;
            // 当前编辑的公司信息;
            this.currentCompanyInfo=row;
            this.title='编辑公司';
            // 调用子组件方法，显示对话框,用setTimeout是为了可以加载添加公司组件;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);  
        },
        // 子组件编辑成功之后，传递给父组件的值;
        editSuccess(editInfo){
            // 替换原有已经被编辑的数据;
            this.companyInfoList.splice(this.companyInfoIndex,1,editInfo);
        },
        //分佣账号设置 
        bankAccount(index, row){
            this.currentCompanyInfo=row;
            this.$refs.commission.open();
        },
        //终止合作,第一次弹框
        handleEnd(index,row){
            this.firstDialogVisible = true,
            this.currentCompanyInfo=row;
            this.remark='';
        },
        //确定终止合作,第二次弹框
        endJoin(){
            // 公司id;
            //将该公司终止合作原因提交，该公司终止合作信息将进入终止合作列表;
        },
        //点击二次对话框取消按钮，继续合作
        continueJoin(){
            this.remark='';
        },
        // 列表信息请求公共函数
        requestList(){
            let self=this;
            // 判断是否选择了省市;
            if(this.form.cityList.length){
                this.form.cityId=this.form.cityList[1];
            };
            let realForm=Object.assign({},this.form,{currentPage:this.currentPage,pageSize:this.pageSize});
            delete realForm.cityList;//删除表单中的cityList选项，因为提交数据时不需要该参数
            delete realForm.searchType;//同上
            this.$http.post(this.$apiUrl.company.list,realForm)
                .then(function(data){
                    self.companyInfoList=data.data.data.data;
                    console.log(data,'12成功');
                })
                .catch(function(err){
                    console.log(err,'失败');
                })
        },
        // summary信息请求
        requestSummary(){
            let self=this;
            // 判断是否选择了城市;
            if(this.form.cityList.length){
                this.form.cityId=this.form.cityList[1];
            };
            let formSummary=Object.assign({},this.form);
            delete formSummary.cityList;
            delete formSummary.searchType;
            this.$http.post(this.$apiUrl.company.summary,formSummary)
                .then(function(data){
                    self.summary=data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    },
    computed:{
        //分页显示多少条数据
        searInfoList(){
            return this.tableData.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize);
        }
    }
}
</script>

<style>
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
    .el-dialog__footer{
        text-align: right;
    }
    .expire .el-form-item__content{
        margin-left: 0 !important;
    }
</style>





